<template>
  <LayoutPage class="page">
    <h1 class="page-title">:nth Tester</h1>

    <div class="box">
      <div class="box__title">Setting Selector</div>
      <div class="box__content">
        <div class="selector">
          <div class="c-select">
            <select id="el" v-model="el">
              <option v-for="(item, index) in elList" :value="item.val" :key="index">{{ item.val }}</option>
            </select>
          </div>

          <div class="c-select">
            <select id="kind" v-model="kind">
              <option v-for="(item, index) in kindList" :value="item.val" :key="index">{{ item.val }}</option>
            </select>
          </div>

          <input id="equation" type="text" v-model.trim="equation">

          <div class="c-btn:tester">
            <div class="c-btn__label">Show</div>
          </div>
        </div>
      </div>
    </div>

    <div class="box">
      <div class="box__title">Formula</div>
      <div class="box__content"></div>
    </div>

    <DialogBase></DialogBase>
  </LayoutPage>
</template>

<script setup>
import { defineOptions, ref } from 'vue'
import LayoutPage from '@/components/layout/page'
import DialogBase from '@/components/dialog/base'

defineOptions({ name: 'NthChildTester' })

const el = ref('li')
const elList = ref([
  { val: 'li' },
  { val: 'div' }
])

const kind = ref(':nth-child')
const kindList = ref([
  { val: ':nth-child' },
  { val: ':nth-of-type' },
  { val: ':nth-last-child' },
  { val: ':nth-last-of-type' },
])

const equation = ref('3n+2')
</script>

<style lang="sass" scoped>
@import @sass/init
@import @sass/helpers

@import ./style.sass
</style>